<template>
    <div>
      <ApexCharts :options="chartOptions" :series="series" type="pie" width="525" />
    </div>
  </template>
  
  <script setup>
  import { ref, watch } from 'vue';
  import ApexCharts from 'vue3-apexcharts'; // Import d'ApexCharts
  
  // Dfinir la prop pour recevoir les donnes des macro-dchets
  const props = defineProps({
    osparData: Object
  });
  
  // Initialisation des sries du graphique
  const series = ref([]);
  
  // Dfinir les options du graphique
  const chartOptions = ref({
    chart: {
      type: 'pie',
      width: 380,
    },
    labels: [
      'Plastique et polystyrne', 
      'Caoutchouc', 
      'Textile', 
      'Papier et carton', 
      'Bois', 
      'Mtal', 
      'Verre', 
      'Poterie et cramique', 
      'Dchets sanitaires', 
      'Dchets mdicaux', 
      'Djections animales', 
      'Autres polluants'
    ],
    responsive: [
      {
        breakpoint: 480,
        options: {
          chart: {
            width: 200
          },
          legend: {
            position: 'bottom'
          }
        }
      }
    ]
  });
  
  // Fonction pour mettre  jour les sries avec les nouvelles donnes
  const updateChart = (data) => {
    if (data) {
      series.value = [
        data.OSPAR_1,
        data.OSPAR_2,
        data.OSPAR_3,
        data.OSPAR_4,
        data.OSPAR_5,
        data.OSPAR_6,
        data.OSPAR_7,
        data.OSPAR_8,
        data.OSPAR_9,
        data.OSPAR_10,
        data.OSPAR_11,
        data.OSPAR_12
      ];
    }
  };
  
  // Observer les changements dans les donnes des macro-dchets et mettre  jour le graphique
  watch(() => props.osparData, (newData) => {
    updateChart(newData); // Met  jour le graphique avec les nouvelles donnes
  });
  </script>